<template>
  <view class="change-password-container">
    <view class="form-item">
      <text class="label">当前密码</text>
      <input class="input" type="password" v-model="currentPassword" placeholder="请输入当前密码" />
    </view>

    <view class="form-item">
      <text class="label">新密码</text>
      <input class="input" type="password" v-model="newPassword" placeholder="请输入新密码" />
    </view>

    <view class="form-item">
      <text class="label">确认新密码</text>
      <input class="input" type="password" v-model="confirmPassword" placeholder="请确认新密码" />
    </view>

    <button class="save-btn" @click="changePassword">修改密码</button>
  </view>
</template>

<script>
export default {
  data() {
    return {
      currentPassword: '',
      newPassword: '',
      confirmPassword: ''
    }
  },
  methods: {
    changePassword() {
      if (!this.currentPassword || !this.newPassword || !this.confirmPassword) {
        uni.showToast({
          title: '请填写所有字段',
          icon: 'none'
        })
        return
      }
      if (this.newPassword !== this.confirmPassword) {
        uni.showToast({
          title: '新密码不匹配',
          icon: 'none'
        })
        return
      }
      // 假设有一个API可以更新密码
      this.$store.dispatch('user/changePassword', {
        currentPassword: this.currentPassword,
        newPassword: this.newPassword
      })
        .then(() => {
          uni.showToast({
            title: '密码修改成功',
            icon: 'success'
          })
          uni.navigateBack()
        })
        .catch(() => {
          uni.showToast({
            title: '密码修改失败',
            icon: 'none'
          })
        })
    }
  }
}
</script>

<style scoped>
.change-password-container {
  padding: 20rpx;
}

.form-item {
  margin-bottom: 20rpx;
}

.label {
  font-size: 28rpx;
  color: #333;
  margin-bottom: 10rpx;
}

.input {
  width: 100%;
  padding: 10rpx;
  font-size: 28rpx;
  border: 1rpx solid #ccc;
  border-radius: 8rpx;
}

.save-btn {
  width: 100%;
  padding: 20rpx;
  font-size: 28rpx;
  color: #fff;
  background-color: #FF6B01;
  border-radius: 8rpx;
  text-align: center;
  margin-top: 20rpx;
}
</style> 